.tile-container {
  display: flex;
  box-sizing: border-box;
  position: relative;
  z-index: 0;
  justify-content: center;
  margin-left: 10rem;
}

.tile-container .tile-1 {
  width: 40rem;
  border-right: 0px solid gray;
}

.tile-container .tile-1 .box {
  margin: 2% 10% 2% 20%;
  width: 70%;
  background-color: #e1e1e1;
  border-radius: 3px;
  max-height: 40rem;
}

.tile-container .tile-1 .box.dark {
  background-color: #333d47;
}

.tile-container .tile-1 .box:hover {
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.24);
  transition: all 0.2s;
}

.tile-container .tile-1 .box .box-header {
  display: flex;
  font-size: 0.8rem;
  height: 3rem;
  align-items: center;
  justify-content: flex-start;
  background-color: #e1e1e1;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  gap: 10px;
}

.tile-container .tile-1 .box .box-header.dark {
  background-color: #333d47;
}

.tile-container .tile-1 .box .box-header figure {
  height: 30px;
  width: 30px;
  margin-left: 2%;
}

.tile-container .tile-1 .box .box-header figure img {
  height: 25px;
  border-radius: 50%;
  margin-top: 3px;
}

.tile-container .tile-1 .box .box-header p {
  flex-grow: 1;
  text-align: left;
  margin-left: 3px;
}

.tile-container .tile-1 .box .box-header.dark p {
  color: #e1e1e1;
}

.tile-container .tile-1 .box .box-header i {
  margin-right: 0.5rem;
}

.tile-container .tile-1 .box .box-header i {
  color: #2e2e2e;
}

.tile-container .tile-1 .box .box-content img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

.tile-container .tile-1 .box .box-footer {
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  font-size: 1.2rem;
  background-color: #e1e1e1;
}

.tile-container .tile-1 .box .box-footer.dark {
  color: #e1e1e1;
}

.tile-container .tile-1 .box .box-footer.dark {
  background-color: #333d47;
}

.tile-container .tile-2 {
  width: 25rem;
}

.tile-container .tile-2 .tile-2-child {
  position: sticky;
  top: 4rem;
}

.tile-2 .side-box {
  /* padding: 5rem 2rem 4rem 4rem; */
  height: 17rem;
}

.tile-container .tile-2 .tile-2-child {
  margin-top: 4.5rem;
}

.tile-container .tile-2 .tile-2-child .side-box {
  margin: 5%;
  width: 15rem;
  min-height: 6rem;
  background-color: #fff;
  border: 1px solid #ebebebfb;
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
}

.tile-container .tile-2 .tile-2-child .side-box.dark {
  background-color: #313b44;
  border: 1px solid #424141fb;
}

.tile-container .tile-2 .tile-2-child .side-box .sb-header {
  background-color: #e1e1e1;
  height: 2.5rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-bottom: 1px solid rgb(192, 191, 191);
}

.tile-container .tile-2 .tile-2-child .side-box .sb-header.dark {
  background-color: #222a33;
  border-bottom: 1px solid rgb(78, 78, 78);
}

.tile-container .tile-2 .tile-2-child .side-box .sb-header p {
  position: relative;
  top: 10px;
  display: flex;
  justify-content: space-around;
}

.tile-container .tile-2 .tile-2-child .side-box .sb-header .sb-head.dark {
  color: #e1e1e1;
}

.tile-2 .tile-2-child .side-box .sb-content {
  height: 100%;
  overflow: hidden;
}

.tile-2 .tile-2-child .side-box .sb-content .image-container {
  height: 8.6rem;
  background-color: wheat;
  overflow: hidden;
}

.tile-2 .tile-2-child .side-box .sb-content .image-container img {
  max-width: 100%;
  object-fit: contain;
}

.tile-2 .tile-2-child .side-box .sb-content .title {
  height: 100%;
}

.tile-2 .tile-2-child .side-box .sb-content .title .title-head {
  font-size: 1rem;
  text-indent: 0.5rem;
  padding: 0.5rem;
  font-weight: 600;
  font-family: 'Times New Roman', Times, serif;
  height: 25%;
  overflow: hidden;
  text-align: center;
}

.tile-2 .tile-2-child .side-box .sb-content .title .time {
  display: inline-block;
  font-size: 0.6rem;
  color: rgb(157, 156, 156);
  text-indent: 10px;
}

.tile-2 .tile-2-child .side-box .sb-content .title a {
  font-size: 0.8rem;
  margin-left: 5%;
  font-weight: 500;
  color: grey;
  cursor: pointer;
  background-color: white;
  border-radius: 16px;
  padding: 2px 5px;
}

.tile-2 .tile-2-child .side-box .sb-content .title a:hover {
  background-color: rgb(242, 242, 242);
  transition: all 0.5s ease;
}

article {
  position: absolute;
  opacity: 0;
  transition: all 0.3s linear;
}

article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}

.bi.bi-chevron-double-right{
border-radius: 16px;
width: 20px;
text-align: center;
transition: all 0.5s ease;
cursor: pointer;

}
.bi.bi-chevron-double-left{
border-radius: 16px;
width: 20px;
text-align: center;
transition: all 0.5s ease;
cursor: pointer;
}
.bi.bi-chevron-double-left:hover{
  background-color: rgba(128, 128, 128, 0.346);
}
.bi.bi-chevron-double-right:hover{
  background-color: rgba(128, 128, 128, 0.346);
}

.power{
  position: sticky;
  top: 50rem;
  left: 60rem;
}

.caption{
  margin-left: .5rem;
  margin-bottom: .25rem;
}

.number-box{
  margin-left: .25rem;
}
/* *******************1. comment box *****************/
.comment-box{
  position: fixed;
  z-index: 1;
  background-color: #fff;
  height: 35rem;
  width: 25rem;
  top: 5rem;
  left: 24.25rem;
  border-radius: 1rem;
}
.cross h2{
  margin: 0rem 0 0 1rem;
  display: inline-block;
}
.cross i{
  margin: 0rem 0 0 14rem;
}
.cross {
  margin: .5rem 0 0 0rem;
}

.comment-field{
  /* background-color: red; */
  margin-top: 1rem;
  display: flex;
  justify-content: center;
}
.comment-field input{
  margin-left: 1rem;
  width:16rem;
  border: none;
  border-bottom: 1px solid;
}
.comment-field button{
  margin-left: 1rem;
  padding: .45rem;
  border-radius: .8rem;
  border: none
}
.comment-field img{
  margin-top: .5rem;
  height: 1rem;
  border-radius: 1rem;
}

.comments{
  margin: 1rem 0 0 1rem;
}

.comment-image{
  position: relative;
}

.comment-image img{
  height: 1rem;
  border-radius: 1rem;
  position: absolute;
  top: .2rem;
}
.user{
  font-size: small;
  display: inline-block;
  margin-left: 1.25rem;
}
